ar y pueda volverse en un cliente difícil.")])]),_c('div',{staticClass:"col-lg-7 col-12"},[_c('img',{staticClass:"mx-3",staticStyle:{"width":"430px","display":"block","margin":"20px"},attrs:{"src":require("@/assets/curso/tema2/8.png"),"alt":""}})])])]),_c('div',{staticClass:"tarjeta back-tab px-5 py-4",attrs:{"titulo":"Con prisa"}},[_c('div',{staticClass:"row px-2 py-2 justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-5 col-12"},[_c('h4',[_vm._v("Con prisa")]),_c('p',{staticClass:"mb-3"},[_vm._v("Se debe ser cuidadoso, si bien a nadie le agradaría que interrumpieran el servicio al cliente por recibir una llamada o enviar un chat, con este tipo de clientes sería más delicado aún. Suelen pensar que ellos son la prioridad y exigen se les atienda rápidamente. Es importante no caer en sus afanes para no cometer errores, pero sí darles el foco de atención según las posibilidades y explicarles muy bien procurando su racionalidad.")])]),_c('div',{staticClass:"col-lg-7 col-12"},[_c('img',{staticClass:"mx-3",staticStyle:{"width":"430px","display":"block","margin":"20px"},attrs:{"src":require("@/assets/curso/tema2/9.png"),"alt":""}})])])])])],1)]),_c('separador'),_vm._m(14),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("Las ofertas son elementos clave para alcanzar el éxito que se requiere como vendedor y en la propia red de apuestas. No necesariamente las ofertas están asociadas a promociones especiales dispuestas por las empresas, constantemente se debe estar ofertando las ventas para alcanzar las metas y aumentar tus posibilidades de éxito. A continuación, algunos trucos para mejorar las ofertas de servicios y apuestas en juegos de suerte y azar (ver tabla 1):")]),_vm._m(15),_vm._m(16)],1)],1)}
